import React, { useState, useEffect } from 'react';
import axios from 'axios';

function AdminDashboard() {
    const [users, setUsers] = useState([]);
    const [newUser, setNewUser] = useState({ username: '', password: '', role: 'USER' });
    const [updateRole, setUpdateRole] = useState({ id: '', role: '' });

    useEffect(() => {
        fetchUsers();
    }, []);

    const fetchUsers = async () => {
        try {
            const response = await axios.get('http://localhost:8080/api/admin/users', {
                auth: { username: 'root', password: 'rootpassword' }
            });
            setUsers(response.data);
        } catch (error) {
            console.error('Li khi ly danh sch ngi dng', error);
        }
    };

    const handleCreateUser = async (e) => {
        e.preventDefault();
        try {
            await axios.post('http://localhost:8080/api/admin/users', newUser, {
                auth: { username: 'root', password: 'rootpassword' }
            });
            fetchUsers();
            setNewUser({ username: '', password: '', role: 'USER' });
        } catch (error) {
            alert('To ngi dng tht bi');
        }
    };

    const handleUpdateRole = async (e) => {
        e.preventDefault();
        try {
            await axios.put(`http://localhost:8080/api/admin/users/${updateRole.id}/role`, updateRole.role, {
                auth: { username: 'root', password: 'rootpassword' }
            });
            fetchUsers();
            setUpdateRole({ id: '', role: '' });
        } catch (error) {
            alert('Cp nht vai tr tht bi');
        }
    };

    return (
        <div className="p-6">
            <h2 className="text-2xl mb-4">Bng iu khin qun tr</h2>

            <div className="mb-8">
                <h3 className="text-xl mb-2">To ngi dng</h3>
                <div className="flex space-x-4">
                    <input
                        type="text"
                        placeholder="Tn ngi dng"
                        value={newUser.username}
                        onChange={(e) => setNewUser({ ...newUser, username: e.target.value })}
                        className="border p-2"
                    />
                    <input
                        type="password"
                        placeholder="Mt khu"
                        value={newUser.password}
                        onChange={(e) => setNewUser({ ...newUser, password: e.target.value })}
                        className="border p-2"
                    />
                    <select
                        value={newUser.role}
                        onChange={(e) => setNewUser({ ...newUser, role: e.target.value })}
                        className="border p-2"
                    >
                        <option value="USER">USER</option>
                        <option value="ADMIN">ADMIN</option>
                        <option value="ROOT_ADMIN">ROOT_ADMIN</option>
                    </select>
                    <button onClick={handleCreateUser} className="bg-blue-500 text-white p-2">To</button>
                </div>
            </div>

            <div className="mb-8">
                <h3 className="text-xl mb-2">Cp nht vai tr ngi dng</h3>
                <div className="flex space-x-4">
                    <input
                        type="text"
                        placeholder="ID ngi dng"
                        value={updateRole.id}
                        onChange={(e) => setUpdateRole({ ...updateRole, id: e.target.value })}
                        className="border p-2"
                    />
                    <select
                        value={updateRole.role}
                        onChange={(e) => setUpdateRole({ ...updateRole, role: e.target.value })}
                        className="border p-2"
                    >
                        <option value="USER">USER</option>
                        <option value="ADMIN">ADMIN</option>
                        <option value="ROOT_ADMIN">ROOT_ADMIN</option>
                    </select>
                    <button onClick={handleUpdateRole} className="bg-blue-500 text-white p-2">Cp nht vai tr</button>
                </div>
            </div>

            <div>
                <h3 className="text-xl mb-2">Danh sch ngi dng</h3>
                <ul>
                    {users.map(user => (
                        <li key={user.id} className="border p-2 mb-2">
                            {user.username} - {user.role}
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
}

export default AdminDashboard;