import React, { useState, useEffect } from 'react';
import axios from 'axios';

function TableManagement() {
  const [tables, setTables] = useState([]);
  const [newTable, setNewTable] = useState({ tableNumber: '', description: '', status: 'AVAILABLE' });

  useEffect(() => {
    fetchTables();
  }, []);

  const fetchTables = async () => {
    try {
      const response = await axios.get('/api/tables');
      setTables(response.data);
    } catch (error) {
      console.error('Li khi ly danh sch bn', error);
    }
  };

  const handleCreateTable = async (e) => {
    e.preventDefault();
    try {
      await axios.post('/api/tables', newTable, {
        auth: { username: 'root', password: 'rootpassword' }
      });
      fetchTables();
      setNewTable({ tableNumber: '', description: '', status: 'AVAILABLE' });
    } catch (error) {
      alert('To bn tht bi');
    }
  };

  return (
    <div className="p-6">
      <h2 className="text-2xl mb-4">Qun l bn</h2>
      
      <div className="mb-8">
        <h3 className="text-xl mb-2">To bn</h3>
        <div className="flex space-x-4">
          <input
            type="number"
            placeholder="S bn"
            value={newTable.tableNumber}
            onChange={(e) => setNewTable({ ...newTable, tableNumber: e.target.value })}
            className="border p-2"
          />
          <input
            type="text"
            placeholder="M t"
            value={newTable.description}
            onChange={(e) => setNewTable({ ...newTable, description: e.target.value })}
            className="border p-2"
          />
          <select
            value={newTable.status}
            onChange={(e) => setNewTable({ ...newTable, status: e.target.value })}
            className="border p-2"
          >
            <option value="AVAILABLE">Cha t</option>
            <option value="OCCUPIED"> t</option>
          </select>
          <button onClick={handleCreateTable} className="bg-blue-500 text-white p-2">To</button>
        </div>
      </div>

      <div>
        <h3 className="text-xl mb-2">Danh sch bn</h3>
        <ul>
          {tables.map(table => (
            <li key={table.id} className="border p-2 mb-2">
              Bn {table.tableNumber} - {table.description} - {table.status === 'AVAILABLE' ? 'Cha t' : ' t'}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default TableManagement;