import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

function Register() {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate();

    const handleRegister = async (e) => {
        e.preventDefault();
        try {
            await axios.post('http://localhost:8080/api/auth/register', { username, password });
            alert('ng k thnh cng');
            navigate('/login');
        } catch (error) {
            alert('ng k tht bi');
        }
    };

    return (
        <div className="flex items-center justify-center min-h-screen">
            <div className="bg-white p-6 rounded shadow-md">
                <h2 className="text-2xl mb-4">ng k</h2>
                <input
                    type="text"
                    placeholder="Tn ngi dng"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className="border p-2 mb-4 w-full"
                />
                <input
                    type="password"
                    placeholder="Mt khu"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="border p-2 mb-4 w-full"
                />
                <button onClick={handleRegister} className="bg-blue-500 text-white p-2 w-full">ng k</button>
            </div>
        </div>
    );
}

export default Register;